.App{
  background: #1A1B21;
  padding: 10px;
  border-radius: 10px;
  width: 320px;
  margin: auto;
  justify-content: center;
}
.footer{
  background: #F5F5F5;
  display: flex;
  justify-content: center;
}
.svg{
  width: 30px;
  height: 30px;
  margin: 10px;
}
.info{
  text-align: center;
}
.name, h2.about, h2.interests{
  color: #FFFFFF;
  font-size: 25px;
  font-weight: 700;
  height: 25px;
}
.job{
  color: #F3BF99;
  font-weight: 400;
  font-size: 13px;
  height: 15px;
}
.website{
  font-weight: 400;
  color: #F5F5F5;
  font-size: 11px;
}
.buttons{
  display: flex;
  justify-content: center;
}
.email{
  border-radius: 6px;
  border: 1px solid #D1D5DB;
  padding: 9px 13px 9px 13px;
  width: 115px;
  cursor: pointer;
  margin: 6px;
  color: #5093E2;
}
.linkedIn{
  background: #5093E2;
  border-radius: 6px;
  padding: 9px 13px 9px 13px;
  width: 115px;
  border: none;
  cursor: pointer;
  margin: 6px;
  color: #FFFFFF;
}
p{
  font-size: 13px;
  font-weight: 400;
  line-height: 150%;
  color: #DCDCDC;
}
.img{
  height: 300px;
}